import React, { useState } from 'react';
import Modal from 'react-modal';
import wzpIcon from '../assets/wzpimage.svg'; // Assicurati che il percorso sia corretto
import telIcon from '../assets/phone.svg'; // Assicurati che il percorso sia corretto
import infoIcon from '../assets/infocontact.svg'; // Assicurati che il percorso sia corretto
import './InfoModal.css';

const InfoModal = ({fromLogin}) => {
  const [infoModalIsOpen, setInfoModalIsOpen] = useState(false);
  
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const handleOpenInfoModal = () => {
    setInfoModalIsOpen(true);
  };

  const handleCloseInfoModal = () => {
    setInfoModalIsOpen(false);
  };

  const handleClearStorageAndRefresh = () => {
    localStorage.clear();
    window.location.reload();
  };

  const handleOpenModal = () => {
    setModalIsOpen(true);
  };

  const handleCloseModal = () => {
    setModalIsOpen(false);
  };

  return (
    <div className="info-modal-container">
      <img src={infoIcon} alt="Info" className="info-icon" onClick={handleOpenInfoModal} />
      <Modal
        isOpen={infoModalIsOpen}
        onRequestClose={handleCloseInfoModal}
        contentLabel="Info Contacts"
        className="modal"
        overlayClassName="overlay"
      >
        <h2>Contatti</h2>
        <h3>Ambulanza</h3>
        <div className="contact-info">
        <div className="contact-item">
            <span>Michela</span>
            <a href="https://wa.me/+393468258260" target="_blank" rel="noopener noreferrer">
              <img src={wzpIcon} alt="WhatsApp" className="contact-icon" />
            </a>
            <a href="tel:+393468258260">
              <img src={telIcon} alt="Telefono" className="contact-icon" />
            </a>
          </div>
          <div className="contact-item">
            <span>Nicolo'</span>
            <a href="https://wa.me/+393468258260" target="_blank" rel="noopener noreferrer">
              <img src={wzpIcon} alt="WhatsApp" className="contact-icon" />
            </a>
            <a href="tel:+393468258260">
              <img src={telIcon} alt="Telefono" className="contact-icon" />
            </a>
          </div>
        </div>
        <h3>Referenti Applicazione</h3>
        <div className="contact-info">
        <div className="contact-item">
            <span>Michela</span>
            <a href="https://wa.me/+393468258260" target="_blank" rel="noopener noreferrer">
              <img src={wzpIcon} alt="WhatsApp" className="contact-icon" />
            </a>
            <a href="tel:+393468258260">
              <img src={telIcon} alt="Telefono" className="contact-icon" />
            </a>
          </div>
          <div className="contact-item">
            <span>Nicolo'</span>
            <a href="https://wa.me/+393468258260" target="_blank" rel="noopener noreferrer">
              <img src={wzpIcon} alt="WhatsApp" className="contact-icon" />
            </a>
            <a href="tel:+393468258260">
              <img src={telIcon} alt="Telefono" className="contact-icon" />
            </a>
          </div>
        </div>
        {fromLogin !== true && <><div className="footer">
            <button onClick={handleOpenModal} className="link">Reset Login</button>
        </div>
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={handleCloseModal}
            contentLabel="Confirm Reset"
            className="modal"
            overlayClassName="overlay"
        >
            <h2>Sei sicuro di voler fare reset del login?</h2>
            <h3>Dopo aver cliccato S dovrai reinserire le credenziali.</h3>
            <h3>Per qualsiasi problema con l'app contattaci ai numeri precedenti!</h3>
            <button onClick={handleClearStorageAndRefresh}>S</button>
            <button onClick={handleCloseModal}>No</button>
        </Modal>
        </>
        }
      </Modal>
    </div>
  );
};

export default InfoModal;
