import React, { useEffect, useState } from 'react';
import './Results.css';
import { appConfig } from '../config';
import moment from 'moment-timezone';

const Results = ({ isAppAvailable }) => {
  const [displayScores, setDisplayScores] = useState([]);

  const fetchScores = async () => {
    try {
      const response = await fetch('https://backend-mover.com/mover/scores');
      if (!response.ok) throw new Error('Errore nel recupero punteggi');

      const data = await response.json();
      const sortedScores = data.sort((a, b) => b.finalScore - a.finalScore);
      setDisplayScores(sortedScores);
      localStorage.setItem('scores', JSON.stringify(sortedScores));
    } catch (error) {
      console.error('Errore:', error);
      alert('Errore nel recupero punteggi');
    }
  };

  useEffect(() => {
    const now = moment().tz('Europe/Rome').toDate();
    const deadlineWithBuffer = moment(appConfig.deadline).add(1, 'hour').toDate();

    if (!isAppAvailable && now >= moment(appConfig.deadline).add(30, 'minutes')) {
      const savedScores = localStorage.getItem('scores');
      if (savedScores) {
        setDisplayScores(JSON.parse(savedScores));
      } else {
        fetchScores();
      }

      const intervalId = setInterval(() => {
        const currentTime = moment().tz('Europe/Rome').toDate();
        if (currentTime <= deadlineWithBuffer) {
          fetchScores();
        } else {
          clearInterval(intervalId);
        }
      }, 15 * 60 * 1000); // 15 minuti in millisecondi

      return () => clearInterval(intervalId);
    }
  }, [isAppAvailable, appConfig.deadline]);

  return (
    <div className="results-container">
      <h2>Risultati</h2>
      {displayScores.length === 0 ? (
        <p className="no-scores">Punteggi in fase di calcolo!</p>
      ) : (
        <ul>
          {displayScores.map((result, index) => (
            <li key={index}>
              {index + 1}. {result.username}: {result.finalScore}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Results;
